import { Link } from "react-router-dom";
import style from "../routes/Home.module.css";

function HomeStudent() {
    return (
        <div className={style.student}>
            <span> / 1</span>
            <button className={style.butt} onClick={()=>{window.location.href="/hackerthon-practice/loginPage"}}><Link className={style.button} to="/hackerthon-practice/loginPage" > / </Link></button>
        </div>
    );
}

export default HomeStudent;